.btnWrapper {
	display: flex;
	align-items: center;
	position: absolute;
	right: 20px;
	top: 440px;
	z-index: 20;
	cursor: pointer;
	transition: var(--transition);
}
@media screen and (min-width: 768px) {
	.btnWrapper {
		position: relative;
		right: 0;
		top: 0;
	}
}
@media screen and (min-width: 1280px) {
	.btnWrapper {
		position: relative;
	}
}
.btnWrapper:hover,
.btnWrapper:focus {
	color: var(--btn-bg-another);
}
.btn {
	width: 80px;
	height: 80px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	border-radius: 50%;
	background-color: var(--btn-bg-another);
	color: #fff;
	transition: var(--transition);
}
.btn:hover,
.btn:focus {
	font-size: medium;
}
@media screen and (min-width: 768px) {
	.btn {
		width: 44px;
		height: 44px;
		margin-left: 12px;
	}
}
.icon {
	width: 21.33px;
	height: 21.33px;
	margin-bottom: 5px;
	transform: scale(1);
	transition: var(--transition);
}

.btn:hover .icon,
.btn:focus .icon {
	transform: scale(1.4);
}
@media screen and (min-width: 768px) {
	.icon {
		width: 14px;
		height: 14px;
		margin-bottom: 0;
	}
}
